/********** Desktop ************/
*, *:after, *:before {
    -ms-box-sizing: border-box;
    box-sizing: border-box; }
  
  html {
    background: white;
    font: bold 14px/20px "Poppins", "Hind Siliguri";
    color: black;
    text-shadow: 0 1px 0 rgba(8, 8, 8, 0.15); }
  
  .error-page-wrap {
    width: 310px;
    height: 310px;
    margin: 100px auto; }
    .error-page-wrap:before {
      box-shadow: 0 0 200px 150px #fff;
      width: 310px;
      height: 310px;
      border-radius: 50%;
      position: relative;
      z-index: -1;
      content: '';
      display: block; }
  
  .error-page {
    width: 310px;
    height: 310px;
    border-radius: 50%;
    top: -310px;
    position: relative;
    text-align: center;
    background: #d36242;
    background: linear-gradient(to bottom, #d36242 0%, darkred 100%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='$firstColor', endColorstr='$secondColor',GradientType=0 ); }
    .error-page:before {
      width: 63px;
      height: 63px;
      border-radius: 50%;
      box-shadow: 3px 25px 0 5px #C95439;
      content: '';
      z-index: -1;
      display: block;
      position: relative;
      top: -19px;
      left: 44px; }
    .error-page:after {
      width: 310px;
      height: 17px;
      margin: 0 auto;
      top: 25px;
      content: '';
      z-index: -1;
      display: block;
      position: relative;
      background: radial-gradient(ellipse at center, rgba(0, 0, 0, 0.65) 0%, rgba(35, 26, 26, 0) 59%, rgba(60, 44, 44, 0) 100%);
      filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#a6000000', endColorstr='#003c2c2c',GradientType=1 ); }
    .error-page h1 {
      color: rgba(255, 255, 255, 0.94);
      font-size: 100px;
      margin: 65px auto 0 auto;
      text-shadow: 0px 0 7px rgba(0, 0, 0, 0.5); }
      .error-page h1:before {
        width: 260px;
        height: 1px;
        position: relative;
        margin: 0 auto;
        top: 70px;
        content: '';
        display: block;
        background: radial-gradient(ellipse at center, rgba(111, 25, 25, 0.65) 0%, rgba(75, 38, 38, 0) 70%, rgba(60, 44, 44, 0) 100%);
        filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#a66f1919', endColorstr='#003c2c2c',GradientType=1 ); }
      .error-page h1:after {
        width: 260px;
        height: 1px;
        content: '';
        display: block;
        opacity: 0.2;
        margin: 0 auto;
        top: 50px;
        position: relative;
        background: radial-gradient(ellipse at center, rgba(247, 173, 148, 0.65) 0%, rgba(255, 255, 255, 0.01) 99%, rgba(255, 255, 255, 0) 100%);
        filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#a6f7ad94', endColorstr='#00ffffff',GradientType=1 ); }
    .error-page h2 {
      margin: 55px 0 30px 0;
      font-size: 17px; }
      .error-page h2:before {
        width: 130px;
        height: 1px;
        position: relative;
        margin: 0 auto;
        top: 31px;
        content: '';
        display: block;
        background: radial-gradient(ellipse at center, rgba(111, 25, 25, 0.65) 0%, rgba(75, 38, 38, 0) 70%, rgba(60, 44, 44, 0) 100%);
        filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#a66f1919', endColorstr='#003c2c2c',GradientType=1 ); }
      .error-page h2:after {
        width: 130px;
        height: 1px;
        content: '';
        display: block;
        opacity: 0.2;
        margin: 0 auto;
        top: 11px;
        position: relative;
        background: radial-gradient(ellipse at center, rgba(247, 173, 148, 0.65) 0%, rgba(255, 255, 255, 0.01) 99%, rgba(255, 255, 255, 0) 100%);
        filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#a6f7ad94', endColorstr='#00ffffff',GradientType=1 ); }
  
  .error-back {
    text-decoration: none;
    color: #430400;
    font-size: 15px; }
    .error-back:hover {
      color: #EB957D;
      text-shadow: 0 0 3px black; }
  